#item{
    color:#ffffff;
    overflow-wrap: break-word;
    word-wrap: break-word;
}

#edit:hover{
    transform: translateX(15px);
    color:seagreen;
    background-color: skyblue;

}
#delete:hover{
    transform: translateX(15px);
    background-color: steelblue;
}
#todoitem{
    background-color:#5CDB95 ;
    opacity:0.6;
    border-radius: 25px;
    
}



@media(max-width: 600px){
    
    #edit{
        width:35%;

    }
    #delete{
        width:25%;
    }
    
    
}
